@font-face {
  font-family: "TT";
  src: url("../fonts/TTNormsPro-Bold.woff2") format("woff2"), url("../fonts/TTNormsPro-Bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT";
  src: url("../fonts/TTNormsPro-Medium.woff2") format("woff2"), url("../fonts/TTNormsPro-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT";
  src: url("../fonts/TTNormsPro-Regular.woff2") format("woff2"), url("../fonts/TTNormsPro-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT";
  src: url("../fonts/TTNormsPro-Light.woff2") format("woff2"), url("../fonts/TTNormsPro-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT";
  src: url("../fonts/TTNormsPro-ExtraLight.woff2") format("woff2"), url("../fonts/TTNormsPro-ExtraLight.woff") format("woff");
  font-weight: 200;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT";
  src: url("../fonts/TTNormsPro-Thin.woff2") format("woff2"), url("../fonts/TTNormsPro-Thin.woff") format("woff");
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}
/* Global */
:root {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.content-box-component {
  box-sizing: content-box;
}

html {
  height: 100%;
  font-family: "TT", Arial, sans-serif;
  font-size: 18px;
  line-height: 1.4;
  color: #282828;
  font-weight: normal;
}

body {
  height: 100%;
  background-color: #f5fafb;
}

button,
input {
  outline: none;
  border: none;
  cursor: pointer;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

ul li,
ol li {
  list-style: none;
}

dd {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
}

.color {
  color: #6ca462;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  position: relative;
  min-height: 100%;
  min-width: 320px;
  overflow: hidden;
  display: grid;
  grid-template-rows: 1fr min-content;
}

.container {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 20px;
}
.container_p {
  padding: 0;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1370px) {
  .container {
    padding: 0;
  }
}

.no-js .wrapper::before {
  content: "Пожалуйста, разрешите использовать JavaScript! Без него сайт грустит...";
  position: fixed;
  top: -300px;
  left: 0;
  width: 100%;
  padding: 20px;
  z-index: 99999;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 500;
  color: #f1535e;
  background-color: rgba(0, 0, 0, 0.75);
  animation-name: real;
  animation-delay: 3s;
  animation-duration: 7s;
  animation-fill-mode: forwards;
}

@keyframes real {
  0% {
    top: -300px;
  }
  10% {
    top: 0;
  }
  90% {
    top: 0;
  }
  100% {
    top: -300px;
  }
}
.socials__list {
  display: flex;
  gap: 10px;
}

.socials__link {
  display: block;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-size: 40px;
}
.socials__link_vb {
  background-image: url("../img/viber.svg");
}
.socials__link_wt {
  background-image: url("../img/whatsapp.svg");
}
.socials__link_tg {
  background-image: url("../img/Telegram.svg");
}

.title {
  font-size: 55px;
  font-weight: 300;
  text-align: center;
}

.title__color {
  color: #6ca462;
}

.title__important {
  font-weight: 600;
  background-image: linear-gradient(to bottom, transparent 0%, transparent 65%, #d5e4ca 65%, #d5e4ca 100%, transparent 100%, transparent 100%);
}

.nav {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.progressbar {
  position: relative;
  height: 60px;
  padding: 7px;
  width: 100%;
  border-radius: 30px;
  background-color: #f5fafb;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.07) inset;
}

.progressbar__line {
  height: 100%;
  width: 16.66%;
  background-image: url("../img/line.png");
  background-size: cover;
  background-repeat: repeat;
  border-radius: 30px;
  opacity: 0.8;
  animation: go 7s infinite linear;
}

.progressbar__status {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: 300;
}

@keyframes go {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 296.2px 0;
  }
}
.header__top {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.hero {
  background: url("../img/bg.png") #fff;
  background-size: cover;
}

.hero__title {
  max-width: 900px;
  font-size: 70px;
  line-height: 1.1;
  font-weight: 600;
  text-align: left;
}

.quiz-wrap {
  position: relative;
  padding-top: 100px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #f5fafb 30%, #f5fafb 100%);
}
.quiz-wrap::before {
  content: "";
  position: absolute;
  bottom: 100%;
  margin-bottom: -1px;
  height: 50px;
  width: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #f5fafb 100%);
}

.quiz-wrap__title {
  max-width: 1300px;
  margin: 0 auto;
}

.quiz-wrap__list {
  display: flex;
  justify-content: space-evenly;
  gap: 20px;
}

.quiz-wrap__item {
  display: flex;
  align-items: center;
  font-size: 22px;
}

.quiz-wrap__img-block {
  position: relative;
  width: 200px;
  height: 200px;
  flex-shrink: 0;
}

.quiz-wrap__img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.quiz-wrap__img_1 {
  transform: translate(-50%, -50%) scale(1.2);
  margin-top: 10px;
}
.quiz-wrap__img_2 {
  transform: translate(-50%, -50%) scale(1.3);
}
.quiz-wrap__img_2 {
  transform: translate(-50%, -50%) scale(1);
}

.quiz-wrap__desc {
  max-width: 220px;
}

.quiz {
  display: flex;
  gap: 20px;
  padding: 60px;
  background-color: #fff;
}

.quiz__left {
  width: 25%;
}

.quiz__right {
  width: 75%;
  padding: 20px 20px 20px 80px;
}

.quiz-wrap__text {
  margin: 10px 0 20px;
  text-align: center;
  font-size: 17px;
  font-weight: 300;
}

.quiz__gift-item {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: space-between;
}

.quiz__gift-icon {
  width: 30px;
  height: 38px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.quiz__gift-icon_price {
  background-image: url("../img/question.png");
}
.quiz__gift-icon_pdf {
  background-image: url("../img/lock.png");
}
.quiz__gift-icon_gift {
  background-image: url("../img/question.png");
}

.quiz__variants {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  row-gap: 60px;
}

.quiz__variant {
  position: relative;
  width: 270px;
  height: 200px;
}

.quiz__variant-desc {
  position: absolute;
  bottom: -45px;
  left: 0;
  width: 100%;
  background-color: #fff;
  padding: 10px;
  text-align: center;
}

.quiz__next {
  margin-top: 40px;
}

.quiz__variant-inner {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  overflow: hidden;
}

.quiz__variant-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  flex-shrink: 0;
}

.quiz__result-icon {
  fill: red;
}

.advantages {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, white 15%, white 100%);
}

.advantages__container {
  display: flex;
}

.advantages__img-block {
  width: 60%;
  flex-shrink: 0;
}

.price__title {
  max-width: 600px;
  text-align: left;
  line-height: 1.1;
}